import React from "react";
import { Link, withRouter } from "react-router-dom";
import styled from "styled-components";

const Header = styled.header`
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0px 10px;
  background-color: rgba(20, 20, 20, 0.8);
  z-index: 10;
  box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.8);
`;

const List = styled.ul`
  display: flex;
`;

const Item = styled.li`
  width: 80px;
  text-align: center;
  &:not(:last-child) {
    margin-right: 10px;
  }
  border-bottom: 3px solid
    ${(props) => (props.currentPage ? "#736AFF" : "transparent")};
`;

const StyledLink = styled(Link)`
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const HeaderComponents = ({ location: { pathname } }) => (
  <Header>
    <List>
      <Item currentPage={pathname === "/"}>
        <StyledLink to="/tournament">Tournament</StyledLink>
      </Item>
      <Item currentPage={pathname === "/history"}>
        <StyledLink to="/history">History</StyledLink>
      </Item>
    </List>
  </Header>
);

export default withRouter(HeaderComponents);
